<template>
  <div id="member-management">
    <!---->
    <div class="card flex-col">
      <div class="card-header">
        <span></span>
        <div class="btn-gray" style="width: 104px; font-size: 14px;" @click="getApplyList"></div>
      </div>
      <div class="card-search flex-row">
        <input class="nameortel" type="text" v-model="nameOrTel" placeholder="" @keyup="searchUser"/>
        <div class="search-btn" @click="searchUser"></div>
      </div>
      <!---->
      <div class="table">
        <div class="table-tr table-header" style="padding: 0 64px;" v-if="searchFlag">
          <div class="w160"></div>
          <div class="w160"></div>
          <div class="w160"></div>
          <div class="w200"></div>
          <div class="w120"></div>
        </div>
        <div class="table-tr" style="padding: 0 64px;" v-for="item in searchUserResult">
          <div class="w160">{{ item.s_username }}</div>
          <div class="w160">{{ item.s_sex === '1' ? '' : item.s_sex === '2' ? '' : ''}}</div>
          <div class="w160">{{ item.s_mybirthday ? date - Number(item.s_mybirthday.slice(0,4)) + '': '' }}</div>
          <div class="w200">{{ item.s_workunti }}</div>
          <div class="w120">
            <span class="blue" @click="addProjectMember(item.s_userid)"></span>
          </div>
        </div>
      </div>
      <!--end-->
      <!---->
      <DialogBox v-if="applyListDialog" :width='640'>
        <h3 slot="header"></h3>
        <div slot="body">
          <div class="table">
            <div class="table-tr table-header" style="padding: 0 32px;">
              <div class="w100"></div>
              <div class="w60"></div>
              <div class="w80"></div>
              <div class="flex"></div>
              <div class="w160"></div>
            </div>
            <div class="table-tr" style="padding: 0 32px;" v-for="item in applyList">
              <div class="w100">{{ item.s_username }}</div>
              <div class="w60">{{ item.s_sex === '1' ? '' : item.s_sex === '2' ? '' : ''}}</div>
              <div class="w80">{{ item.s_mybirthday ? date - Number(item.s_mybirthday.slice(0,4)) : '' }}</div>
              <div class="flex">{{ item.s_workunti }}</div>
              <div class="w160 flex-row">
                <span class="blue mg-r-20" @click="handleApply(item.id, '1')"></span>
                <span class="red" @click="handleApply(item.id, '2')"></span>
              </div>
            </div>
          </div>
        </div>
        <div slot="footer">
          <div class="btn-gray" @click="applyListDialog = false"></div>
        </div>
      </DialogBox>
      <!--end-->
    </div>
    <!---->
    <div class="card" >
      <div class="card-header"></div>
      <div class="table flex-col">
        <div class="table-tr table-header">
          <div class="w60">ID</div>
          <div class="w160"></div>
          <div class="w160"></div>
          <div class="flex"></div>
          <div class="w220"></div>
        </div>
        <div class="table-body" :style="{height: tableHeight + 'px'}">
          <div style="padding-bottom: 10px" v-if="loading"><Loading></Loading></div>
          <div class="table-tr" v-for="(member, index) in memberList">
            <div class="w60">{{ index + 1 }}</div>
            <div class="w160">{{ member.s_username }}</div>
            <div class="w160">
              <span v-text="member.centername"></span>
            </div>
            <div class="flex">{{ member.task }}</div>
            <div class="w220 flex-row">
              <span class="blue mg-r-20" @click="addToCenterDialog = true, currentId = member.u_userID"></span>
              <span class="blue mg-r-20" @click="addTaskDialog = true, currentId = member.u_userID, checkedTaskId = member.taskid"></span>
              <span class="red" @click="deleteMember(member.u_userID, index)"></span>
            </div>
          </div>
        </div>
        <DialogBox v-if="addToCenterDialog" :width='360'>
          <h3 slot="header"></h3>
          <div slot="body" class="option">
            <div v-for="option in centerList" style="height: 32px;">
              <input type="radio" :id="option.u_centerID" v-model="checkedCenterId" :value="option.u_centerID"/>
              <label :for="option.u_centerID">{{ option.u_centername }}</label>
            </div>
          </div>
          <div slot="footer">
            <div class="btn-blue" @click="addMemberToCenter"></div>
            <div class="btn-gray" @click="addToCenterDialog = false, checkedCenterId = ''"></div>
          </div>
        </DialogBox>
        <DialogBox v-if="addTaskDialog" :width='360'>
          <h3 slot="header"></h3>
          <div slot="body" class="option">
            <div v-for="option in taskList">
              <input type="checkbox" :id="option.taskid" v-model="checkedTaskId" :value="option.taskid"/>
              <label :for="option.taskid">{{ option.taskname }}</label>
            </div>
          </div>
          <div slot="footer">
            <div class="btn-blue" @click="addTask"></div>
            <div class="btn-gray" @click="addTaskDialog = false, checkedTaskId = []"></div>
          </div>
        </DialogBox>
      </div>
    </div>
  </div>
</template>
<script>
  // import $ from 'webpack-zepto'
  import API from '../api.js'
  import DialogBox from '../lib/dialog.vue'
  import Loading from '../lib/loading.vue'
  export default {
    data () {
      return {
        loading: false,
        memberList: [],
        centerList: [],
        taskList: [],
        applyList: [],
        currentId: '',
        currentTask: '',
        date: new Date().getFullYear(),
        addMemberDialog: false,
        applyListDialog: false,
        nameOrTel: '',
        searchFlag: false,
        searchUserResult: [],
        checkedCenterId: '',
        checkedTaskId: [],
        addToCenterDialog: false,
        addTaskDialog: false,
        tableHeight: 0
      }
    },
    components: {
      DialogBox,
      Loading
    },
    mounted () {
      this.getMemberList()
      this.getCenterList()
      this.getTaskList()
      const tableTop = this.$el.getElementsByClassName('table-body')[0].getBoundingClientRect().top
      this.tableHeight = window.innerHeight - tableTop - 30
    },
    methods: {
      // 
      getMemberList () {
        this.loading = true
        API.GetMember({userid: window.sessionStorage.getItem('userid'), projectid: this.$route.params.projectid}).then((response) => {
          this.memberList = response
          this.loading = false
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 
      getCenterList () {
        API.GetProjectCenter({userid: this.$root.userid, projectid: this.$route.params.projectid}).then((response) => {
          this.centerList = response
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 
      getTaskList () {
        API.GetTaskList({userid: this.$root.userid, projectid: this.$route.params.projectid}).then((response) => {
          this.taskList = response
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 
      getApplyList () {
        this.applyListDialog = true
        API.GetApplyList({userid: this.$root.userid, projectid: this.$route.params.projectid}).then((response) => {
          this.applyList = response
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 
      handleApply (id, status) {
        API.HandleNotice({userid: this.$root.userid, noticeid: id, status: status}).then(response => {
          if (!response) { return }
          this.getApplyList()
        }).catch(err => {
          this.toast({
            text: err,
            type: 'error',
            placement: 'top'
          })
        })
      },
      /*  */
      addProjectMember (userid) {
        API.AddProjectMember({userid: window.sessionStorage.getItem('userid'), projectid: this.$route.params.projectid, memberid: userid}).then((response) => {
          this.toast({
            type: 'success',
            text: '',
            placement: 'top'
          })
          this.getMemberList()
          this.addMemberDialog = false
          this.nameOrTel = ''
          this.searchUserFlag = false
          this.adminRank = ''
        }).catch((err) => {
          let type = err === '' ? 'default' : 'error'
          err = err === '' ? '' : ''
          this.toast({
            type: type,
            text: err,
            placement: 'top'
          })
        })
      },
      /*  */
      searchUser () {
        if (this.nameOrTel) {
          API.FindUserInfo({userId: window.sessionStorage.getItem('userid'), projectId: this.$route.params.projectid, nameOrTel: this.nameOrTel}).then((response) => {
            this.searchUserResult = response
            this.searchFlag = true
          }).catch((err) => {
            window.alert(err)
          })
        }
      },
      /*  */
      addMemberToCenter () {
        if (this.checkedCenterId) {
          this.addToCenterDialog = false
          API.AddMemberToCenter({userid: this.$root.userid, projectid: this.$route.params.projectid, centerid: this.checkedCenterId, memberid: this.currentId}).then((response) => {
            this.toast({
              type: 'success',
              text: '',
              placement: 'top'
            })
            this.getMemberList()
            this.checkedCenterId = ''
          }).catch((err) => {
            this.checkedCenterId = ''
            let type = err === '' ? 'default' : 'error'
            err = err === '' ? '' : ''
            this.toast({
              type: type,
              text: err,
              placement: 'top'
            })
          })
        } else {
          this.toast({
            type: 'warning',
            text: '',
            placement: 'top'
          })
        }
      },
      /*  */
      addTask () {
        this.addTaskDialog = false
        API.AddTask({userid: this.$root.userid, projectid: this.$route.params.projectid, taskid: this.checkedTaskId.join(','), memberid: this.currentId}).then((response) => {
          this.toast({
            type: 'success',
            text: '',
            placement: 'top'
          })
          this.getMemberList()
          this.checkedTaskId = []
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '',
            placement: 'top'
          })
        })
      },
      /*  */
      deleteMember (memberId, index) {
        let vm = this
        this.confirm({
          title: '',
          message: '',
          onConfirm () {
            API.DeleteProjectMember({userid: vm.$root.userid, projectid: vm.$route.params.projectid, memberid: memberId}).then((response) => {
              vm.memberList.splice(index, 1)
              vm.toast({
                type: 'success',
                text: '',
                placement: 'top'
              })
            }).catch(() => {
              vm.toast({
                type: 'error',
                text: '',
                placement: 'top'
              })
            })
          }
        })
      }
    }
  }
</script>
<style scoped>
  .option{
    padding-left: 32px;
    line-height: 32px;
  }
  .card-search {
    padding: 24px 32px;
    width: 448px;
    position: relative;
  }
  .card-search input{
    width: 448px;
  }
  .table-tr{
    padding: 0 32px;
  }
  .table-tr.table-header{
    padding-right: 40px;
  }
</style>


